@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=Roboto');





.App {
  text-align: left;
  margin: 2% 0% 5% 0%;
}

.ant-carousel .slick-slide {
  text-align: center;
  height: 80vh;
  line-height: 160px;
  background: white;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}

img {
height: 100%;
width : 100%;
object-fit: cover;
}

#description-project {
  position: relative;
  height: 70vh;
  margin: 5% 5% 5% 5%;
}

#tag {
  position: absolute;
  bottom: 10px;
}
